<template>
    <grid-layout
        :layout.sync="layout"
        :row-height="rowHeight"
        :is-draggable="isDraggable"
        :is-resizable="isResizable"
        :vertical-compact="verticalCompact"
        :use-css-transforms="useCssTransforms"
        :col-num="colNum"
        :margin="margin">
        <grid-item v-for="(item, index) in layout" :key="item.i"
            :x="item.x"
            :y="item.y"
            :w="item.w"
            :h="item.h"
            :i="item.i"
            :style="{backgroundColor: backgroundColor}"
            @resize="resize"
            @move="move"
            @resized="resized"
            @moved="moved">
            <button @click="clicked(index)"></button>
            <component :is="item.type" :item-info="item"></component>
        </grid-item>
    </grid-layout>
</template>

<script>
    import GridItem from './components/GridItem.vue';
    import GridLayout from './components/GridLayout.vue';

    import Widget from './components/Widget.vue';

    export default {
        name: 'app',
        components: {
            // ResponsiveGridLayout,
            GridLayout,
            GridItem,
        },
        addWidgetType(type, widget){
            this.components[type] = {
                ...widget,
                extends: Widget
            }
        },
        props: {
            layout: Array, // 
            isDraggable: { // 
                type: Boolean,
                default: true
            },
            isResizable: { // 
                type: Boolean,
                default: true
            },
            rowHeight: { // 
                type: Number,
                default: 150
            },
            maxRows: { // 
                type: Number,
                default: Infinity
            },
            margin: [10, 10], // 
            verticalCompact: { // 
                type: Boolean,
                default: true
            },
            useCssTransforms: {
                type: Boolean,
                default: true
            },
            colNum: {
                type: Number,
                default: 12
            },
            dragIgnoreFrom: {
                type: String,
                default: 'a, button'
            },
            dragAllowFrom: {
                type: String,
                default: 'a, button'
            },
            resizeIgnoreFrom: {
                type: String,
                default: 'a, button'
            },
            backgroundColor: {
                type: String,
                default: 'rgba(255,255,255,1)'
            }
        },
        data () {
            return {
                // rowHeight: 150,
                // colNum: 12,
                // index: 0,
                // margin: [3, 3],
                // opacity: 100,
                // bgcolor: '#eee'
            }
        },
        mounted: function () {
            this.index = this.layout.length;
        },
        methods: {
            clicked: function(index) {
                this.layout.splice(index, 1);
                // window.alert("CLICK!");
            },
            increaseWidth: function() {
                let width = document.getElementById("content").offsetWidth;
                width += 20;
                document.getElementById("content").style.width = width+"px";
            },
            decreaseWidth: function() {
                let width = document.getElementById("content").offsetWidth;
                width -= 20;
                document.getElementById("content").style.width = width+"px";
            },
            removeItem: function(item) {
                //console.log("### REMOVE " + item.i);
                this.layout.splice(this.layout.indexOf(item), 1);
            },
            addItem: function() {
                // let self = this;
                //console.log("### LENGTH: " + this.layout.length);
                let item = {"x":0,"y":0,"w":2,"h":2,"i":this.index+"", whatever: "bbb"};
                this.index++;
                this.layout.push(item);
            },
            move: function(i, newX, newY){
                console.log("MOVE i=" + i + ", X=" + newX + ", Y=" + newY);
            },
            resize: function(i, newH, newW, newHPx, newWPx){
                console.log("RESIZE i=" + i + ", H=" + newH + ", W=" + newW + ", H(px)=" + newHPx + ", W(px)=" + newWPx);
            },
            moved: function(i, newX, newY){
                console.log("### MOVED i=" + i + ", X=" + newX + ", Y=" + newY);
            },
            resized: function(i, newH, newW, newHPx, newWPx){
                console.log("### RESIZED i=" + i + ", H=" + newH + ", W=" + newW + ", H(px)=" + newHPx + ", W(px)=" + newWPx);
            }
        },
    }
</script>
